.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.skeleton{
}
.SkeletonDisplayText{
    height: 3.2rem;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    will-change: opacity;
    position: relative;
    max-width: 12rem;
    width: 12rem;
    animation: polaris-SkeletonShimmerAnimation .8s linear infinite alternate;
}
.SkeletonDisplayText::after{
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: block;
      background-color: #dfe3e8;
      border-radius: 3px;
}
@keyframes polaris-SkeletonShimmerAnimation {
    0% {
      opacity: .45;
    }
    100% {
      opacity: .9;
    }
  }